import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import ImgTxt from '@ui/imgtxt';
import ImgTxtInverse from '@ui/imgtxt_inverse';
import Video from '@ui/video';
//import Video from '@ui/video';


const Intro = () => (
  <StaticQuery
    query={graphql`
      query {
        Earphone4all: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "earphone4all" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Unidesign: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "unidesign" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Allcolour: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "allcolour" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Noise: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "noise" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Mobileeq: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "mobileeq" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Ui: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "ui" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Conversation: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "conversation" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Stickers: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "stickers" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Anywhere: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "parkour" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="intro">
        <SectionHeader 
          header="獨一無二，為所有聽力狀態設計" 
          subheader="我們正在啟動一個募資專案，要創造一款嶄新的耳機。不論你是聽常者、或配戴助聽器、電子耳，我們都為你設想。"
          toggle2Act="true"
          text="我們的 Indiegogo 預熱頁面"
          href="https://www.indiegogo.com/projects/hai-world-s-first-bluetooth-earphones-for-all/coming_soon/x/23571765"
          target="_blank"
        />
        <Video videoSrcURL="https://www.youtube.com/embed/OpiWKbHBuAU" videoTitle="main video"/>
        <ImgTxt
          header="完美解決助聽器跟電子耳使用者的困擾" 
          text="入耳式耳機跟頭戴型耳機，無法跟助聽器、電子耳一同運作。原因很簡單——一個耳朵無法同時佩戴輔聽器跟別的產品。"   
        >
          <Img fluid={data.Allcolour.childImageSharp.fluid} />
        </ImgTxt>
        <ImgTxtInverse 
          header="無障礙與模組化" 
          text="Rearia Hai 是第一款針對助聽器、電子耳使用者跟聽常者設計的通用耳機。模組化的 neckcoil 技術，讓你可以隨心所欲的配戴。兩邊都戴助聽器、只戴一邊助聽器、或通通不戴，都沒問題！你可以輕鬆隨需求選擇入耳單體或是 Telecoil 線圈。" 
        >
          <Img fluid={data.Unidesign.childImageSharp.fluid} />
        </ImgTxtInverse>
        <ImgTxt 
          header="體驗優秀音質" 
          text="不要小看 Telecoil ，因為它夠老，所以大多數助聽器跟電子耳都有它。我們讓這個多功能的技術走得更遠，提供卓越音質、動聽享受。"
        >
          <Img fluid={data.Earphone4all.childImageSharp.fluid} />
        </ImgTxt>
        <ImgTxtInverse 
          header="不再聽見嗡嗡嗡" 
          text="我們對聆聽體驗近乎苛求。針對助聽器在 Telecoil 模式下會聽到的環境干擾，我們開發世界第一的降噪技術——動態噪訊消除（Dynamic Buzz Cancellation）。它讓你享受音樂，不被飛機上或變電箱附近的怪聲打擾。" 
        >
          <Img fluid={data.Noise.childImageSharp.fluid} />
        </ImgTxtInverse>   
      </Section>
    )}
  />
);

export default Intro;
